.recent-books {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.recent-books .book-item {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.recent-books .book-title {
  margin-top: 0;
  color: #333;
}
.recent-books .book-author,
.recent-books .book-publication-date {
  margin: 5px 0;
}